export default {
    name: "app-image",
    props: {
        imgSrc: String,
        width: String,
        height: String,
        mode: {
            type: String,
            default() {
                return 'aspectFill';
            }
        },
        borderRadius: String,
    },
    data() {
        return {
            is_loading: true,
            is_error: false
        };
    },
    computed: {
        appBackground() {
            return `width: ${this.width};height: ${this.height};`;
        },
        imgStyle() {
            return `border-radius: ${this.borderRadius}`;
        },
    },

    methods: {
        imgError() {
            this.is_error = true;
        },
        imgLoad() {
            this.is_loading = false;
        }
    }
}
